import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;
import Vue from 'vue'
import App from './App'

//导入网络请求的包
import { $http } from '@escook/request-miniprogram'

uni.$http = $http

//网络请求的要路径
$http.baseUrl = 'https://api-ugo-web.itheima.net'

//请求拦截器
$http.beforeRequest = function(option){
	uni.showLoading({
		title:"数据加载中..."
	})
}

//响应拦截器
$http.afterRequest = function(option){
	uni.hideLoading()
}

uni.$showMsg = function(title='数据加载失败！', duration = 1500){
	uni.showToast({
		title,
		duration,
		icon:'none'
	})
	
}

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()